<template>
	<div class="InventoryQuery">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>库存查询</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<span>库位代码</span>
				<input  placeholder="请输入内容"  ref="LibraryCode"  @change="toUppercase('LibraryCode')"  @focus="g_focus('LibraryCode')"    @keyup.enter="sendParams('LibraryCode')"   v-model="LibraryCode" ></input>
				<i v-if="LibraryCode" @click="clearInput('LibraryCode')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>商品代码</span>
				<input  placeholder="请输入内容"  ref="CommodityCode"   @change="toUppercase('CommodityCode')"   @focus="g_focus('CommodityCode')"   @keyup.enter="sendParams('CommodityCode')"  v-model="CommodityCode"></input>
				<i v-if="CommodityCode" @click="clearInput('CommodityCode')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>商品名称</span>
				<input  placeholder="请输入内容"  ref="descr"  style="text-transform:none;"   @focus="g_focus('descr')"    @keyup.enter="sendParams('descr')"  v-model="descr"></input>
				<i v-if="descr"  @click="clearInput('descr')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>客户代码</span>
				<input  placeholder="请输入内容"  ref="CustomerCode"   @change="toUppercase('CustomerCode')"  @focus="g_focus('CustomerCode')"   @keyup.enter="sendParams('CustomerCode')"  v-model="CustomerCode"></input>
				<i v-if="CustomerCode"  @click="clearInput('CustomerCode')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>托盘编号</span>
				<input  placeholder="请输入内容"  ref="PalletNum"   @change="toUppercase('PalletNum')"  @focus="g_focus('PalletNum')"   @keyup.enter="sendParams('PalletNum')"  v-model="PalletNum"></input>
				<i v-if="PalletNum"  @click="clearInput('PalletNum')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>收货单号</span>
				<input  placeholder="请输入内容" ref="ReceiptNum"   @change="toUppercase('ReceiptNum')"  @focus="g_focus('ReceiptNum')"   @keyup.enter="sendParams('ReceiptNum')"  v-model="ReceiptNum"></input>
				<i v-if="ReceiptNum"   @click="clearInput('ReceiptNum')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>系统批次</span>
				<input  placeholder="请输入内容"  ref="SystemBatch"  @change="toUppercase('SystemBatch')"   @focus="g_focus('SystemBatch')"   @keyup.enter="sendParams('SystemBatch')" v-model="SystemBatch"></input>
				<i v-if="SystemBatch"  @click="clearInput('SystemBatch')" class="iconfont icon-cuowu"></i>
			</div>	
			<span  class="g_singleBtn"   @click="sendParams('LibraryCode')">查询</span>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p   @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"InventoryQuery",
		data(){
			return{
				LibraryCode:"",//库位代码    6010210
				CommodityCode:"",//商品代码     1010001000
				descr:"",//商品名称      怡纯,350ml,1×24
				CustomerCode:"",//客户代码     YIB02
				PalletNum:"",//托盘编号     P1804260001
				ReceiptNum:'',//收货单号     0000002903
				SystemBatch:"",//系统批次    0000000408
				listData:{},
				tipsHide:false,//提示信息是否显示
				inputFocus:"",
				msg:'',//
			}
		},
		mounted() {

			this.$refs['LibraryCode'].focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		}, 
		created(){

			console.log(this.$store.state.inventoryCondition)
			var inventoryCondition = this.$store.state.inventoryCondition;

			this.LibraryCode = inventoryCondition.LibraryCode;
			this.CommodityCode = inventoryCondition.CommodityCode;
			this.descr = inventoryCondition.descr;
			this.CustomerCode = inventoryCondition.CustomerCode;
			this.PalletNum = inventoryCondition.PalletNum;
			this.ReceiptNum = inventoryCondition.ReceiptNum;
			this.SystemBatch = inventoryCondition.SystemBatch;
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				
				this[inputName] = qrcode;
				
				this.sendParams();
				
			},
			//提交 查询
			sendParams:function(inputName){

				var that = this;

				if(!(this.LibraryCode || this.CommodityCode ||  this.descr ||  this.CustomerCode ||  this.PalletNum ||  this.ReceiptNum ||  this.SystemBatch)){
					that.tipsShow(inputName);

					that.inputFocus = inputName;

					that.msg = "请至少填写一个信息";

					return;
				}

				var currentUrl = this.HOST + "RFInvQuery.do";

				var postData = {
					"Loc":this.LibraryCode,
					"Sku":this.CommodityCode,
					"Descr":this.descr,
					"StorerKey":this.CustomerCode,
					"PID":this.PalletNum,
					"Lottable10":this.ReceiptNum,
					"Lot":this.SystemBatch,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){

						//将库存查询条件 存储到全局
						that.$store.state.inventoryCondition = {
							LibraryCode:that.LibraryCode,
							CommodityCode:that.CommodityCode,
							descr:that.descr,
							CustomerCode:that.CustomerCode,
							PalletNum:that.PalletNum,
							ReceiptNum:that.ReceiptNum,
							SystemBatch:that.SystemBatch
						}
						// 判断是从库存查询页面跳转而来 还是从移动页面跳转而来
						that.$store.state.InventoryQuery = true;
						that.$store.state.RFInvMove = false;

						that.$store.state.QueryListData = res.data.data;

						//跳转到库存详情页面
						that.$router.push({
							name:"InventoryDetails",
							query:{
							}
						})

					}else{

						that.tipsShow(inputName);

						that.inputFocus = inputName;

						that.msg = "未查询到数据";
					}
				},inputName)
			},
			//返回上一页  首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			}
		}
	}
</script>
<style scoped lang="less">
	.InventoryQuery{
		.container{
			padding-top: 1/50rem;
			input::placeholder{
				font-size: 25/50rem;
			}
			input{
				font-size: 25/50rem;
			}
			&>div{
				width:100%;
				height: 55/50rem;
				margin-top: 20/50rem;
				padding: 0 20/50rem;
				box-sizing: border-box;
				position: relative;
				span{
					height: 55/50rem;
					line-height: 55/50rem;
					margin:0  5/50rem;
					text-align: right;
				}
				input{
					margin-left: 10/50rem;
					width:510/50rem;	
					height:55/50rem;
					padding:0 10/50rem;
					box-sizing: border-box;
				}
				i{
					position:absolute;
					right:90/50rem;
					font-size: 28/50rem;
					top: 13/50rem;
				}
				
			}
			&>div:nth-child(7){
				margin-bottom: 30/50rem;
			}
		}
	}
	@media screen and (max-width: 330px){
		.InventoryQuery{
			.container{
				input::placeholder{
					font-size: 35/50rem;
				}
				input{
					font-size: 35/50rem;
				}
				&>div{
					width:100%;
					height: 65/50rem;
					margin-top: 10/50rem;
					span{
						height: 65/50rem;
						line-height: 65/50rem;
					}
					input{
						margin-left: 0;
						height: 65/50rem;
					}
					i{
						right:70/50rem;
						font-size: 35/50rem;
						top: 14/50rem;
					}
				}
			}
		}
	}

	@media screen and (max-width: 277px){
		.InventoryQuery{
			.container{
				input::placeholder{
					font-size: 40/50rem;
				}
				input{
					font-size: 40/50rem;
				}
				&>div{
					width:100%;
					height:75/50rem;
					margin-top: 20/50rem;
					span{
						margin:0;
						height: 75/50rem;
						line-height: 75/50rem;
					}
					input{
						width: 490/50rem;
						margin-left: 0;
						height: 75/50rem;
					}
					i{
						right:60/50rem;
						font-size: 45/50rem;
						top: 15/50rem;
					}
				}
			}
		}
	}
</style>


